@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Syne+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Title{
    text-transform: uppercase;
    color: white;
    font-size: 1.3rem;
    position: relative;
    padding-bottom: 1rem;
}
.Title::after{
    content: "";
    background-color: rgba(0, 0, 255, 0.473);
    width: 11rem;
    height: 0.3rem;
    left: 0;
    bottom: 0;
    position: absolute;
}
.Title::before{
    content: "";
    background-color: blue;
    width: 4rem;
    height: 0.3rem;
    left: 0;
    bottom: 0;
    position:absolute;
}
.Title span{
    color: blue;
    position: absolute;
    top: 10%;
    left: 0;
    font-size: 3rem;
    opacity: 0.2;
}
.NavBar{
    height: 100vh;
}
.nav {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1rem;
    margin-top: 1rem;
}
.profile{
    width: 100%;
    text-align: center;
    margin-top: 1rem;
    border-bottom: 2px solid rgb(56, 56, 56);
}

.nav-item{
    list-style-type: none;
    /* text-align: center; */
    /* position: relative; */
}
.nav-item a {
    text-decoration: none;
    font-size: inherit;
    display: block;
    color: inherit;
    padding: 0.5rem 0.3rem;
    position: relative;
}
.nav-item a::before{
    content: "";
    position: absolute;
    /* top: 0;
    left: 0;
    width: .4rem;
    height: 100%; */
    overflow: hidden;
}
.nav-item a:hover{
    color: rgb(146, 146, 146);
    width: 100%;
    
}
img{
    width: 70%;
    border-radius: 100%;
    margin-bottom: 1rem;
}
footer{
    width: 100%;
}
footer p{
    text-align: center;
    padding: 1rem 0;
}
.active{
  background-color: #222938;
  border-radius: 10px;
}
.name{
    padding: 2rem 2rem;
}
.coursework{
    padding: 2rem 2rem;
}
.contact{
    padding: 1rem 1rem;
    display: -webkit-flex;
    display: flex;
    margin: 0;
    min-height: 100vh;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.contact-list{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.contact-list li {
    border: 3px solid gray;
    display: inline;
    padding: 0.5rem 0.5rem;
}
.contact-list h3{
    color: red;
}
.skillsContainer{
    width: 60%;
    font-size: 0.7rem;
    border: 4px solid grey;
    padding: 1rem 1rem;
    margin: 0.4rem 0.4rem;
}
.skill-title{
    font-size: 1rem;
    font-weight: 400;
    padding: 0.4rem 0.4rem;
}

.skill-bar{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0rem 0.4rem;
}
        
.skill-progress{
    width: 100%;
}
.progress{
    width: 100%;
    height: .5rem;
    background-color: red;
    margin-left: 1rem;
    background-color: #0381ff3f;
    position: relative;
}
.inner-pregress{
    position: absolute;
    background-color: #037FFF;
    height: 100%;
    bottom: 0;
    left: 0;
}
           
        
    

.work-container{
    padding: 1rem 1rem;
}
.work-container div{
    padding: 0.4rem 0.4rem;
}
.work-container h4{
    color: rgb(228, 118, 118);
}
.work-container .date{
    font-size: 0.8rem;
}
.cert-container{
    margin: 1rem 1rem;
}
td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 1rem;
}
.cert-container li{
    list-style: none;
}
.cert-container a{
    color: white;
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Roboto', sans-serif;
    color: #ffffff;
    /* font-size: 1.2rem; */
}

.side-bar{
    width: 20%;
    height: 100vh;
    background-color: #191D2B;
    position: fixed;
}
.main-content{
    width: 80%;
    /* min-height: 100vh; */
    height: 100vh;
    margin-left: 20%;
    /* position: fixed; */
    background-color: black;
    position: relative;
    display: grid;
}
.content{
    margin: 1rem 2rem;
}

.pro-text{
    font-size: 0.6rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.pro-text a{
    color: rgb(247, 97, 97);
}


/* typography for main-content */

.Home{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width: 80%;
}
.main-text{
    text-align: center;
    font-size: 2rem;
}
.main-text span{
    color: rgb(255, 0, 0);
   font-family: 'Syne Mono', monospace;
}

.home-para{
    text-align: center;
    font-size: 0.8rem;
}

.icons-lists{
    padding: 2rem 2rem;
    -webkit-align-items: center;
            align-items: center;
    display: grid;
    grid-template-columns: repeat(3,1fr);
}
.icons-lists li{
    list-style-type: none;
    border:2px solid rgb(158, 158, 158);
    padding: 1rem 1rem;
}
.icons-item div{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}
.icons-item div a{
    text-decoration: none;
    color: white;
    font-size: 1.4rem;
    cursor: pointer;
}
.icon-text{
    font-size: 0.7rem;
}
